<script>
	import cheader from '../../components/common/header';
	import footers from '../../components/common/footer';
	export default {
		data() {
			return {
				stass: ' ',
				password: null,
				contains_lovercase: false,
				contains_number: false,
				contains_uppercase: false,
			}
		},
		methods: {
			p_len: function() {
				this.password_length = this.password.length;
				if(this.password_length < 7) {
					console.log(this.password_length)
				}

				if(this.password_length > 3) {
					this.typed = true;
					this.stass = '简单'
				} else if(this.password_length <= 0) {
					this.stass = ' '
				} else {
					this.stass = '太短'
					this.typed = false;
				}

				this.contains_lovercase = /[a-z]/.test(this.password);
				this.contains_number = /\d/.test(this.password);
				this.contains_uppercase = /[A-Z]/.test(this.password);

				if(this.contains_lovercase == true) {
					this.stass = '简单'
				}

				if(this.contains_lovercase == true && this.contains_number == true || this.contains_lovercase == true && this.contains_uppercase == true || this.contains_number == true && this.contains_uppercase == true) {
					this.stass = '一般'
				}

				if(this.contains_lovercase == true && this.contains_number == true && this.contains_uppercase == true) {
					this.stass = '非常好'
				}

			}
		},
		mounted: function() {

		},
		components: {
			cheader,
			footers
		}
	}
</script>
